container{
    height: 100vh;
    padding: 7rem;
    overflow: hidden;
}

.header__container h5{
font-size: 1.1rem;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*CV links*/

.cvlink{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*Socials*/

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: -31rem;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*Me picture*/

.me{
    background: linear-gradient(var(--color-primary ), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/*Scroll down*/

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: -27rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* Media Queries for Medium device)*/
@media screen and (max-width: 1024px) { 
 .container{
          height: 68vh;
 }
 .scroll__down{
    bottom: 18rem;
 }
 .header__socials{
    bottom: 15rem;
 }

}
/* Media Queries for small device)*/
@media screen and (max-width: 600px) { 
    .container{
        height: 100vh;
}
.header__socials, .scroll__down{
        display: none;
}

}